import { Component, Input, OnInit } from '@angular/core';

@Component({
  selector: 'app-gauge-bar',
  templateUrl: './gauge-bar.component.html',
  styleUrls: ['./gauge-bar.component.scss']
})
export class GaugeBarComponent implements OnInit {
  @Input()
  miniPalier: number;

  @Input()
  bronzePalier: number;

  @Input()
  argentPalier: number;

  @Input()
  orPalier: number;

  @Input()
  tips: number;

  maxGauge: number;
  currentGauge: number;

  containInterval: any;

  constructor() {

  }

  ngOnDestroy(): void {
    //Called once, before the instance is destroyed.
    //Add 'implements OnDestroy' to the class.
    clearInterval(this.containInterval);
  }

  ngOnInit(): void {
    this.containInterval = setInterval(() => {
      if (this.miniPalier) {
        // this.maxGauge = this.miniPalier + this.bronzePalier + this.argentPalier + this.orPalier + 0;
        this.maxGauge =  this.orPalier + 5;
        this.currentGauge = Math.floor((this.tips * 100) / this.maxGauge);
      }
    }, 2000)
  }



}
